body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --bg-color:/*#160D5F;*/#7a0000;
}
a:visited,a:link{
  text-decoration: none;
  background-color: white;
  color:black;
  font-family: 'Open Sans', Helvetica, sans-serif;
  font-size:.75em;
  padding: 0em 1em 0em 1em;
  display:inline-block;
  border-radius: .5em;
}
a:hover{
  background-color: #c5c5c5;
}

@media (min-width:320px){
  body{
  	/*background-color: #009999;*/
    background-color: #7a0000;
    background-color: var(--bg-color);
  	font-size: 100%;
  }
  #root{
      width: 100%;
      top:20%;
      left:50%;
      margin-left:-50%;
      margin-right: -50%;
      position: absolute;
  }
  #title{
      width: 100%;
      max-width: 100%;
      margin: 0 auto;
      text-align: center;
      font-family: 'Open Sans', Helvetica, sans-serif;
      font-size:2em;
      color:#fff;
      letter-spacing: .125em;
      margin-bottom: .5em;
  }
  #games{
      width: 100%;
      max-width: 90%;
      margin: 0 auto;
      text-align: center;
  }
  .select-field{
    display:inline-block;
    width: 100%;
    background-color: rgb(121, 18, 0);
    border-radius: .5em;
    margin: 0 auto;
  }

  .select-field > select{
    float:left;
    width:100%;
    background-color: transparent;
    border:0em;
    padding:.5em;
    font-size: 1em;
    color:#fff;
    background: url(../../static/media/menu_arrow.b34fd0b5.svg) no-repeat 97% 50%;
    -moz-appearance:none;
    -webkit-appearance:none;
    appearance:none;
  }

  .select-field > select > option{
    color:black;
  }

  #translator{
  	width: 100%;
    max-width: 90%;
    margin: 0 auto;
  }
  #translator > input{
    width:100%;
    display: inline-block;
    padding: .5em;
    border: 0em;
    border-radius: .5em;
    font: normal 1em/0 'Open Sans', Helvetica, sans-serif;
    background: rgba(255,255,255,1);
    -webkit-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
    transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
  }
  #text{
      height:2em;
      width: 100%;
      position: relative;
      margin: 0 auto;
      text-align: center;
      font-family: 'Open Sans', Helvetica, sans-serif;
      color:#e0e0e0;
      font-size:1em;
  }
  input:focus{
    border: .125em solid rgb(255, 8, 0);
  }

  #output{
      width: 100%;
      max-width:95%;
      margin: 0 auto;
      text-align: center;
  }
  .images{
  	position: relative;
  	top: 0;
  }
  .images span{
  	font-family: 'Open Sans', Helvetica, sans-serif;
  	font-size: .5em;
  	display: inline-block;
  	vertical-align: middle;
    margin-left:0;
    margin-right:0;
    color:white;
  }
  .images .small{
      vertical-align: middle;
      width: 1em;
      height: 1em;
      margin-left:0;
      margin-right:0;
  }
  .images .medium{
      vertical-align: middle;
      width: 1.5em;
      height: 1em;
      margin-left:0;
      margin-right:0;
  }
  .images .large{
      vertical-align: middle;
      width: 2em;
      height: 1em;
      margin-left:0;
      margin-right:0;
  }
  img:hover{
     background-color:rgb(255, 255, 255);
  }
} 

